import { ADDIDTOCART, CARTCOUNT } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async ['addToCart']({ commit, state }, config) {
        if (config) {
            //const data = await api.addToCart(config)
            const obj = {
                id: config.id,
                cb: config.cb
            }
            commit(ADDIDTOCART, obj);
        }
    },
    async ['getCartId']({ commit, state }, config) {
        if (config) {
            const data = await api.getCartId(config)
            const obj = {
                data,
                cb: config.cb
            }
            if (data) {
                commit(ADDIDTOCART, obj);
            }
        }
    },
    async ['getCartCount']({ commit, state }, config) {


        var db = openDatabase("CartDB", "1.0", "购物车", 1024 * 1024);
        if (db) {
            db.transaction(function(context) {
                context.executeSql("SELECT * FROM cartTable", [], function(
                    context,
                    results
                ) {
                    var len = results.rows.length;

                    commit(CARTCOUNT, len)
                    config.cb(len)
                });
            });
        }
    }
}

export default actions


