<template>
<div class="footer-bar">
   <router-link class="al" to="/"><span class="iconfont al-icon-home"></span><span>首页</span></router-link>
   <router-link class="al" to="/cart"><span class="iconfont al-icon-cart"><i class="o">{{c}}</i></span><span>购物车</span></router-link>
   <router-link class="al" to="/user"><span class="iconfont al-icon-user"></span><span>我的</span></router-link>
</div>
</template>
<script>
export default {
  data(){
    return {
        count : 0
    }
  },
  computed:{
      c : {
           get:function(){ 
              return this.$store.state.global.count
           }
      }
  },
  created() {
    var self = this
    self.$store.dispatch("getCartCount", {
      cb: function(data) {
        self.count = data;
      }
    });
  }
};
</script>
<style>
.footer-bar {
  height: 46px;
  display: flex;
  line-height: 20px;
  border-top: 1px solid #ededed;
  background: #fff;
}
.footer-bar a.al {
  font-size: 12px;
  color: #333;
  padding-top: 5px;
  text-align: center;
  flex: 1;
  font-size: 12px;
}
.footer-bar a.al span {
  font-size: 20px;
  line-height: 20px;
  display: block;
}
.footer-bar a.al span:nth-child(2) {
  font-size: 12px;
}
.footer-bar a.al span.icon-user {
  font-size: 18px;
}
.footer-bar a.al span .o{
	border-radius: 10px;
	padding: 0 2px;
	background: #f40000;
	font-size: 12px;
	font-style: normal;
	vertical-align: middle;
	text-align: center;
	color: #ffffff;
}
.footer-bar a.al.router-link-exact-active{
   color: #f40000;
   background: #f7f7f7;
} 
</style>


