import { ADDIDTOCART, CARTCOUNT } from './type'
const mutations = {
    [ADDIDTOCART](state, data) {
        if (data) {
            //state.userBuyGoodItem = state.userBuyGoodItem.concat([data.id]);
            var obj = JSON.stringify({
                item: state.userBuyGoodItem
            })
            var db = openDatabase("CartDB", "1.0", "购物车", 1024 * 1024);
            if (db) {
                db.transaction(function(trans) {
                    trans.executeSql("CREATE TABLE IF NOT EXISTS cartTable (Gid)", [], function(trans, result) {
                        trans.executeSql('SELECT * FROM cartTable WHERE Gid=?', [data.id], function(trans, result) {
                            if (result.rows.length < 1) {
                                trans.executeSql('INSERT INTO cartTable (Gid) VALUES ("' + data.id + '")');
                            }
                        })
                    }, function(trans, message) { console.log(message); });
                })
            }
            data.cb();
            //window.localStorage.setItem('userBuyGoodItem',obj);
        }
    },
    [CARTCOUNT](state, data) {
        state.count = data
    }

}
export default mutations


